html {
  font-size: 14px;
  font-weight: 400; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "PFRegalDisplayPro", sans-serif;
  color: #000; }

main {
  flex-grow: 1; }

a {
  color: #000;
  text-decoration: none; }

svg {
  width: auto;
  height: auto; }

input,
button {
  -webkit-appearance: none; }

input,
button,
select,
textarea {
  font-family: "PFRegalDisplayPro", sans-serif;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0; }

input {
  padding: 0;
  margin: 0;
  font-size: 18px; }
  @media only screen and (max-width: 1080px) {
    input {
      font-size: 11px; } }
  input::placeholder {
    font-family: "PFRegalDisplayPro", sans-serif;
    font-size: 14px;
    color: #000; }
    @media only screen and (max-width: 1080px) {
      input::placeholder {
        font-size: 11px; } }
  input:focus::placeholder {
    opacity: 0; }

.container {
  width: 1028px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%;
  box-sizing: border-box; }
  @media only screen and (max-width: 767px) {
    .container {
      padding: 0 20px; } }

/* partials */
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 202px;
  padding: 13.5px 40px;
  border: 2px solid #de7087;
  background-color: #fff;
  border-radius: 60px;
  font-family: "PragmaticaLightC", sans-serif;
  font-size: 13px;
  text-transform: uppercase;
  color: #de7087;
  transition: background-color 0.3s ease, color 0.3s ease; }
  @media (min-width: 1080px) {
    .button:hover, .button:focus {
      background-color: #de7087;
      color: #fff; } }

/* blocks */
.first-screen {
  background-repeat: no-repeat;
  background-size: cover;
  padding: 17px 0 20px;
  position: relative; }
  .first-screen__title {
    font-family: "PragmaticaLightC", sans-serif;
    font-size: 58px;
    line-height: 60px;
    font-weight: 300;
    text-align: center; }
    .first-screen__title span {
      font-family: "PFRegalDisplayPro", sans-serif;
      font-size: 70px; }
  .first-screen__image {
    position: relative;
    margin-top: 24px; }
  .first-screen__btn {
    max-width: 230px;
    margin: 26px auto 0;
    font-size: 18px; }
  .first-screen__logo {
    position: absolute;
    bottom: 60px; }
    .first-screen__logo--1 {
      left: 7.6%; }
    .first-screen__logo--2 {
      right: 7%; }

.show-block {
  max-width: 300px;
  width: 100%;
  padding-bottom: 30px;
  border: 1px solid #a5c7cf;
  background-color: #fff;
  position: absolute;
  top: 146px;
  right: 24px; }
  .show-block__image {
    width: 81%;
    margin: 0 auto;
    position: relative; }
  .show-block__woman {
    width: 100%;
    height: 100%;
    object-fit: cover; }
  .show-block__ball {
    position: absolute;
    left: -15px;
    bottom: 0; }
  .show-block__title {
    font-size: 39px;
    line-height: 36px;
    text-align: center;
    color: #000;
    margin-top: 24px; }
    .show-block__title span {
      font-family: "PragmaticaLightC", sans-serif;
      font-size: 30px;
      line-height: 36px; }
  .show-block__btn {
    margin: 20px auto 0; }
